import React from "react";

const Social = (props) => {
  return (
    <a href={props.url} target="_blank" rel="noreferrer">
      <img
        src={props.logo}
        className="w-8 md:w-16 transform duration-500 hover:scale-125"
        alt={props.social}
      />
    </a>
  );
};

export default Social;
