import * as React from "react";
import { useInView } from "react-intersection-observer";

const Work = (props) => {
  const { ref, inView } = useInView({
    threshold: 0.2,
  });

  const [animate, startAnimate] = React.useState(0);
  React.useEffect(() => {
    inView ? startAnimate(true) : startAnimate(false);
  }, [inView]);

  return (
    <div ref={ref} className={`py-2 px-8 flex flex-col items-center md:flex-row gap-4 border-2 rounded-md shadow-md ${animate ? "animate-fade-in" : ""}`}>
      <div className="w-40 h-40">
        <img src={props.img} alt={props.company} />
      </div>
      <div className="flex-grow flex flex-col justify-center">
        <p className="font-sans text-lg">{props.company}</p>
        <p className="text-gray-500">{props.position}</p>
        <p className="text-gray-500">{props.location}</p>
        <p className="text-gray-500">{props.time}</p>
        <p className="text-gray-500">{props.description}</p>
      </div>
    </div>
  );
};

export default Work;
