import React from "react";
import linkedin from "../assets/SocialLogos/linkedin.svg";
import gmail from "../assets/SocialLogos/gmail.svg";
import github from "../assets/SocialLogos/github.svg";
import instagram from "../assets/SocialLogos/instagram.svg";
import Social from "./social";

const SocialSection = () => {
  return (
    <div className="w-11/12 h-40 md:w-2/3 rounded-md flex flex-row justify-evenly items-center py-2">
      <Social
        url="https://www.linkedin.com/in/dz365"
        logo={linkedin}
        social="LinkedIn"
      />
      <Social
        url="https://www.github.com/dz365"
        logo={github}
        social="GitHub"
      />
      <Social url="mailto:dwqz365@gmail.com" logo={gmail} social="Email" />
      <Social
        url="https://www.instagram.com/dz.2372"
        logo={instagram}
        social="Instagram"
      />
    </div>
  );
};

export default SocialSection;
